// import  from ""
import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import empty from "@/components/mycom/empty";
export default {
	data() {
		return {
			showPos: false,
			tabCurrentIndex: 0,
			integral:0,
			navList: [{
					state: 0,
					text: '全部',
					dataList: [],
					status: 'loading',
					load: false
				},
				{
					state: 1,
					text: '收入',
					dataList: [],
					status: 'loading',
					load: false
				},
				{
					state: 2,
					text: '支出',
					dataList: [],
					status: 'loading',
					load: false
				}
			]
		}
	},
	components: {
		uniLoadMore,
		empty
	},
	onLoad() {
		this.loadData();
	},
	onShow() {},
	onHide() {},
	methods: {
		changeTarba(e) {
			this.tabCurrentIndex = e.target.current;
			this.initData(this.tabCurrentIndex);
		},
		loadData() {
			console.log("到底")
			let num = this.tabCurrentIndex;
			let parmas = {
				uid: getApp().globalData.uid,
				type: num,
				nowPage: this.page
			}
			this._reqw.ipost(parmas, "integralMingxiList").then(res => {
				this.navList[num].status="more"
				console.log(res)
				res.result == 0 ? (this.totalPage = res.totalPage,this.integral=res.integral, res.dataList == 0 ? this.navList[num].load =
					true : res.dataList
					.forEach(item => {
						this.navList[num].dataList.push(item)
					})) : this.$api.tip(res.resultNote)
			}).catch(err => {})

		},
		tabClick(index) {
			this.tabCurrentIndex = index;
		},
		initData(index) {
			this.page = 1;
			this.totalPage = 1;
			this.navList[index].dataList = [];
			this.loadData();
		},
		loadDataMore() {
			let num = this.tabCurrentIndex;
			this.totalPage > this.page ? (this.page += 1, this.loadData()) : setTimeout(() => {
				this.navList[index].status = "noMore"
			}, 30)
		}

	}

}
